* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    overscroll-behavior: none;

    font-family: "Ubuntu", sans-serif;

    --md-ref-typeface-plain: 'Ubuntu';
    --md-icon-font: 'Material Symbols Rounded';
    --md-focus-ring-color: transparent;

    -webkit-user-drag: none;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    margin: auto;
    min-height: 100vh;
    max-width: 1200px;

    display: grid;
    grid-template: 148px 1fr / 1fr;

    background-color: var(--md-sys-color-background);
    color: var(--md-sys-color-on-surface);
}

header {
    display: flex;
    gap: 24px;
    padding: 24px;

    img {
        height: 100%;
        border-radius: 12px;
    }

    h1 {
        margin: 0;
        font-size: 84px;
        color: var(--md-sys-color-primary);
    }
}

main {
    height: 100%;
    padding: 48px 24px;

    max-width: 800px;
    justify-self: center;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    ol {
        padding-top: 18px;
        position: relative;

        &>span {
            position: absolute;
            right: 0;
            top: 0;
            color: var(--md-sys-color-outline-variant);
            font-size: 14px;
        }

        li {
            position: relative;

            &::marker {
                color: var(--md-sys-color-on-surface-variant);
                font-size: 24px;
            }

            &::before {
                content: '';
                position: absolute;
                inset: 0;
                background-color: var(--md-sys-color-primary);
                border-radius: 12px;
                z-index: -1;

                opacity: 0;
                transition: opacity 0.14s ease-in-out;
            }

            &:hover {
                &::before {
                    opacity: 0.06;

                }
            }

            a {
                display: flex;
                flex-direction: column;
                gap: 14px;

                padding: 24px 12px;

                h4 {
                    margin: 0;
                    font-size: 32px;

                    transition: font-size 0.24s ease-in-out;
                }

                div {
                    color: var(--md-sys-color-on-surface-variant);
                    overflow: hidden;
                    max-width: 600px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    width: 100vw;
                }

                span {
                    color: var(--md-sys-color-outline-variant);
                    font-size: 12px;
                }

            }
        }
    }
}

.pagination {
    display: flex;
    gap: 12px;
    margin: 12px 0;
    justify-content: space-evenly;
}